.logo {
  height: 220px;
  position: relative;
  top: 40px; }
  .logo__container {
    width: 300px;
    height: 300px;
    background-color: #232444;
    margin: auto;
    border-radius: 200px; }

.social {
  margin: auto;
  margin-top: 6px;
  display: inline-block; }
  @media (max-width: 767px) {
