.App {
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: 'Work Sans', sans-serif;
  text-rendering: optimizeLegibility;
  color: white;
  letter-spacing: 0em;
  border-top: 10px solid #b7e4cf; }
  @media (max-width: 767px) {
    .App {
      overflow: scroll;
      height: auto;
      width: 100%;
      display: flex;
      flex-direction: column-reverse; } }

.content {
  height: calc(100% - 10px);
  position: absolute;
  background-color: #1b1a33;
  width: 50%;
  min-width: 720px;
  overflow-y: scroll; }
  @media (max-width: 767px) {
    .content {
      height: auto;
      min-width: unset;
      overflow: hidden;
      width: 100%;
      position: relative; } }
  .content .container {
    padding: 25px 80px;
    position: relative;
    box-sizing: border-box;
    height: auto; }
    @media (max-width: 767px) {
      .content .container {
        padding: 25px 30px; } }

.slider {
  height: calc(100% - 10px);
  position: absolute;
  width: 50%;
  overflow: hidden;
  right: 0; }
  @media (max-width: 1400px) {
    .slider {
      width: calc(100% - 720px); } }
  @media (max-width: 767px) {
    .slider {
      position: relative;
      height: calc(100vw * 25 / 27);
      width: 100%; } }
  .slider__img {
    opacity: 1;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    position: absolute;
    -webkit-transition: opacity 1s linear;
    transition: opacity 1s linear;
    width: 100%;
    height: 100%;
    object-fit: cover; }
    @media (max-width: 767px) {
      .slider__img {
        position: absolute;
        width: 100%;
        height: auto; } }
    .slider__img--hidden {
      opacity: 0; }

h1 {
  font-size: 40px;
  font-weight: 400;
  text-align: left;
  margin-bottom: 5px; }

.vol__info {
  text-align: left;
  line-height: 1.44;
  font-size: 18px;
  margin-top: 7px; }
  .vol__info--top {
    margin-top: 10px; }

.vol__form {
  padding: 40px 0; }

.vol__check {
  text-align: left;
  margin-bottom: 30px; }
  .vol__check input {
    position: absolute;
    z-index: 999;
    opacity: 0;
    width: 18px;
    height: 18px;
    cursor: pointer; }
    .vol__check input:checked + .vol__check__box:after {
      visibility: visible; }
  .vol__check__label {
    vertical-align: middle;
    font-size: 18px;
    line-height: 1.44;
    cursor: pointer; }
    .vol__check__label svg {
      font-size: 12px;
      vertical-align: middle;
      margin-left: 10px; }
  .vol__check__box {
    position: relative;
    height: 18px;
    width: 18px;
    border: 2px solid #b7e4cf;
    display: inline-block;
    border-radius: 2px;
    margin-right: 30px;
    vertical-align: middle;
    cursor: pointer; }
    .vol__check__box:after {
      content: "";
      position: absolute;
      visibility: hidden;
      left: 5px;
      top: -8px;
      width: 8px;
      height: 18px;
      border: solid white;
      border-width: 0 4px 4px 0;
      -webkit-transform: rotate(40deg);
              transform: rotate(40deg); }
  .vol__check__desc {
    padding-top: 30px;
    font-size: 14px;
    line-height: 1.43; }
    .vol__check__desc ul {
      line-height: 2; }

.vol__error {
  visibility: hidden;
  text-align: right;
  color: #b61b28;
  font-size: 14px; }

.vol__inputs {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 10px; }
  @media (max-width: 767px) {
    .vol__inputs {
