.vol__input-container {
  text-align: left;
  width: 47%;
  margin-bottom: 15px; }
  .vol__input-container label {
    display: block;
    padding-bottom: 8px; }
  .vol__input-container--full {
    width: 100%; }
  @media (max-width: 767px) {
    .vol__input-container {
      width: 100%; } }

.vol__text-input {
  border: solid 2px white;
  background-color: #1b1a33;
  color: white;
  font-size: 18px;
  line-height: 1.44;
  padding: 7px 15px;
  border-radius: 1px;
  box-sizing: border-box;
  width: 100%; }
  .vol__text-input::-webkit-input-placeholder {
    color: white;
    opacity: 0.4; }
  .vol__text-input::-moz-placeholder {
    color: white;
    opacity: 0.4; }
  .vol__text-input:-ms-input-placeholder {
    color: white;
    opacity: 0.4; }
  .vol__text-input::-ms-input-placeholder {
    color: white;
    opacity: 0.4; }
  .vol__text-input::placeholder {
    color: white;
    opacity: 0.4; }
  .vol__text-input:focus {
    border-color: #b7e4cf;
    outline: none; }
  .vol__text-input--required:valid:not(:focus) {
    background-image: url(/static/media/check.42a32373.svg);
    background-repeat: no-repeat;
    background-position: right;
    background-origin: content-box; }
  .vol__text-input--visited:invalid {
    border-color: #b61b28;
    background-image: url(/static/media/UI-alert-circle.48bed385.svg);
    background-repeat: no-repeat;
    background-position: right;
    background-origin: content-box;
    background-size: 24px 16px; }
    .vol__text-input--visited:invalid + div.vol__error {
      visibility: visible; }

.vol__submit {
  margin: auto; }

.vol__flash {
  position: fixed;
  top: 0;
  color: white;
  background-color: #b61b28;
  width: 100%;
  padding: 10px;
  left: 0;
  z-index: 1; }

.vol__cancel {
